body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.navbar
{
  margin-bottom:30px;
}
.active-cyan-4 input[type=text]:focus:not([readonly]) {
  border: 1px solid #2692f7;
  box-shadow: 0 0 0 1px #2692f7;
}
.active-cyan-3 input[type=text] {
  border: 1px solid #2692f7;
  box-shadow: 0 0 0 1px #2692f7;
}
.page-active{
    background: rgb(240, 98, 73) !important;
    -webkit-transition: 0.2s ease-in-out;
    transition: 0.2s ease-in-out;
}
#pageNav{
    background: none !important;
}
.page-btn {
    height: 50px;
    width: 70px;
    border-radius: 5px;
    margin: 0px 5px;
    background: #42474b;
    color: white;
}
#pageNav > ul > li{
    padding: 13px;
}

.card-container {
    text-align: left;
    height: 300px;
    margin: 50px 0px;
    -webkit-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    position: relative;
}

.thecard {
    position: absolute;
    width: 90%;
    height: 100%;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
}
.details {
    top: 310px;
    position: absolute;
}
.details > p {
    margin: 0px;
    color: white;
}
.details > small {
    margin: 5px;
    font-size: 13px;
    color: rgba(255, 255, 255, 0.5);
}
.front-card > img {
    border-radius: 5px;
    height: 300px;
    width: 100%;
    box-shadow: -8px 8px #26252544;
}

.front-card > span {
    position: relative;
    display: block;
    background: rgb(230, 158, 24);
    height: 60px;
    padding: 18px;
    width: 60px;
    border-radius: 50%;
    text-align: center;
    top: -100px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    left: 80%;
    color: white;
    font-weight: 700;
}

.front-card {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    color: rgb(67, 67, 67);
}
.back-card {
    padding: 20px;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgb(52, 49, 49);
    border-radius: 5px;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.back-card > span {
    position: absolute;
    top: -10px;
    right: -10px;
}
.back-card > span > i {
    color: rgb(235, 65, 65);
}
.back-card > p {
    font-size: 9px;
    word-spacing: 1.5px;
    color: #cccccc;
}
.back-card > h5 {
    margin-bottom: 4px;
    font-size: 16px;
    color: #cccccc;
}

.show-card {
    z-index: 3;
    -webkit-transform: scale(2);
            transform: scale(2);
}
.show-card > .thecard {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.list-item {
    text-align: left;
    background: #42474b !important;
    color: white;
}
.list-item-active {
    border-left-color:rgb(240, 98, 73);
    border-left-width: 2px;
    -webkit-transition: 0.3s ease-in;
    transition: 0.3s ease-in;
}
.search {
    width: 100%;
}
.inputt {
    text-align: left;
}
.inputt > label {
    margin: 0px 0px;
    color: aliceblue;
}
.inputt > div > #input {
    width: 100%;
    padding: 10px 40px;
    height: 50px;
    background: #42474b;
    border-bottom-color: rgb(240, 98, 73);
    border-top: 0px;
    border-right: 0px;
    border-left: 0px;
    place-content:  rgb(240, 98, 73);
    border-radius: 3px;
    color: white;
}
.inputt > div > #input:focus {
    outline: none;
}
.inputt > div .input-icon { 
    position: relative;
    color: wheat;
    z-index: 2;
    text-align: left;
    top: 36px;
    left: 15px;
}

.navbar-list {
    margin: 0px;
}
.navbar-list > li > a {
    color:rgb(254, 66, 110);
}
.navbar-list > li > a:hover {
    text-decoration: none;
}
.nav-brand {
    letter-spacing: 3px;
    display: inline-block;
    font-weight: 500;
    font-size: 40px;
    padding: 10px;
    color: rgb(255, 43, 91);
}
.nav-brand:hover{
    text-decoration: none;
    color: rgb(249, 43, 91);
}
nav{
    background: #292a2b; 
}
nav > ul{
    margin: 0px;
}
nav > ul > li {
    display: inline-block;
    list-style: none;
    letter-spacing: 1.5px;
    padding: 0px;
}
nav > ul > li > a {
    padding: 20px 40px;
    display: block;
}
nav > ul > li > a:hover {
    background: #181818;
    cursor: pointer;
    -webkit-transition: 0.2s ease-in;
    transition: 0.2s ease-in;
}








