.special-btn {
  color: white;
  padding: 15px 50px;
  border: 0px;
  border-radius: 50px;
  background: #FF416C;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #FF4B2B, #FF416C);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left bottom, left top, from(#FF4B2B), to(#FF416C));
  background: -webkit-linear-gradient(bottom, #FF4B2B, #FF416C);
  background: linear-gradient(to top, #FF4B2B, #FF416C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.pink {
